
import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import { Row, Col} from 'antd';




class SC extends React.Component {


    render() {

        return (
            <div>
                <Row gutter={16}>
                    <Col xs={24} sm={24} md={12} lg={8} xl={8}>
                        <div className="gutter-box">
                         <span>
                            <div style={{position: "relative"}}>
                                <Link to="shardinglist">
                            　　     <img style={{height: "150px", width: "300px"}} src="https://gitee.com/nick070809/pics/raw/master/home/kk13.png"/>
                            　　     <div style={{position: "absolute", left: "6%", top: "34%"}}><span style={{fontSize: "40px"}}>Sharding-Jdbc</span></div>
                                </Link>
                            </div>
                        </span>
                        </div>
                    </Col>


                </Row>
            </div>

        );
    }
}

export default SC;




